
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
body, html, header, nav, footer, form, div, p, ul, li, dl, dt, dd, ol, a, input,button, span, img, h1,h2,h3,h4,h5,h6,i, em {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
html {
    -webkit-text-size-adjust: none;
}
body {
    font-family: "SF Pro SC", "SF Pro Text", "SF Pro Icons", "PingFang SC", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    font-synthesis: none;
    -moz-font-feature-settings: 'kern';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2e2e2e;
    font-size: 14px;
}
h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
}
li {
    list-style-type: none;
}
input, button, textarea {
    border: none;
    outline: none!important;
&:focus {
        outline: none!important;
}
}
img {
    border: none;
    
    // 去基线
    display: block;
    vertical-align: top;
}
img[src=""] {
    opacity: 0;
}
a {
    color: #343434;
    text-decoration: none;
&:hover {
        text-decoration: none;
        outline: none!important;
}
}
a:focus {
    outline: none;
}
i, em {
    font-style: normal;
}
a {
    display: block;
    width: 100%;
    height: 100%;
    cursor: pointer;
}
img {
    width: 100%;
    height: auto;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    color: #d6d6d6;
    font-size: small;
    font-weight: bold;
}
input:-moz-placeholder, textarea:-moz-placeholder {
    color: #d6d6d6;
    font-size: small;
    font-weight: bold;
}
input::-moz-placeholder, textarea::-moz-placeholder {
    color: #d6d6d6;
    font-size: small;
    font-weight: bold;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    color: #d6d6d6;
    font-size: small;
    font-weight: bold;
}
span {
    display: block;
}
button,
input[type="button"],
input[type="submit"] {
    display: block;
    text-align: center;
    font-weight: bold;
    cursor: pointer;
}
button:active,
input[type="button"]:active {
    background-color: #3D96E2;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
input[type="number"] {
    -moz-appearance: textfield;
}
select {
    cursor: pointer;
    outline: none;
    border: 0;
}
.mask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: saturate(180%) blur(2px);
    backdrop-filter: saturate(120%) blur(2px);
    transition: none;
    -moz-transition: none;
    -webkit-transition: none;
    -o-transition: none;
    z-index: 0;
}
.close-btn {
    width: 32px;
